/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(11, 11, 112);
  font-family: 'Lato', sans-serif;
  color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  background-size: cover;
  background-position: center;
  transition: background-image 0.5s ease-in-out;
}

/* App Container */
.App {
  background-color: rgba(231, 231, 240, 0.8);  /* Slight transparency for contrast */
  width: 100%;
  max-width: 500px;
  border-radius: 15px;
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.1);
  padding: 30px;
  text-align: center;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

/* Header */
.app-name {
  font-size: 40px;
  font-weight: 800;
  color: #2b8e8e;
  text-transform: uppercase;
  margin-bottom: 20px;
  letter-spacing: 2px;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
}

/* Search Bar */
.search-bar {
  position: relative;
  margin-bottom: 30px;
}

.city-search {
  width: 100%;
  padding: 16px 20px;
  font-size: 18px;
  border: 2px solid #76c7c0;
  border-radius: 30px;
  outline: none;
  background: #f7f9fc;
  color: #333;
  transition: all 0.3s ease-in-out;
}

.city-search:focus {
  border-color: #2b8e8e;
  box-shadow: 0 0 10px rgba(43, 142, 142, 0.5);
}

/* Loader */
.Oval {
  margin: 20px auto;
}

/* Error Message */
.error-message {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #e74c3c;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 1px;
}

.error-message svg {
  margin-right: 10px;
}

.error-message span {
  font-size: 20px;
  font-weight: 500;
  color: #e74c3c;
}

/* Weather Information */
.city-name h2 {
  font-size: 36px;
  font-weight: 700;
  color: #2b8e8e;
  margin-bottom: 10px;
  letter-spacing: 1px;
}

.city-name span {
  font-size: 20px;
  color: #333;
}

.date {
  font-size: 18px;
  color: #7f8c8d;
  margin-bottom: 20px;
  letter-spacing: 0.5px;
}

.icon-temp {
  font-size: 64px;
  font-weight: 700;
  margin-bottom: 15px;
  color: #2b8e8e;
  transition: all 0.3s ease-in-out;
}

.icon-temp img {
  vertical-align: middle;
  margin-right: 15px;
}

.deg {
  font-size: 28px;
  color: #e74c3c;
}

.des-wind p {
  font-size: 18px;
  color: #7f8c8d;
  margin: 5px 0;
}

/* Add smooth animations to the components */
.city-name h2,
.date,
.icon-temp,
.des-wind {
  opacity: 0;
  animation: fadeIn 0.8s forwards;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

/* Responsive Design */
@media (max-width: 600px) {
  .App {
    padding: 20px;
  }

  .app-name {
    font-size: 30px;
  }

  .city-search {
    padding: 14px 18px;
    font-size: 16px;
  }

  .city-name h2 {
    font-size: 28px;
  }

  .icon-temp {
    font-size: 56px;
  }

  .deg {
    font-size: 24px;
  }
}
